<template>
  <div class="blog-post upper-accent" style="padding: 5vmin 10vmin 5vmin 10vmin;">
    <v-row no-gutters="" align="center" justify="center">
      <v-col xl="8" lg="9" md="10" sm="11" cols="12">
        <p :class="changeDescClasses + ' mb-2'"> {{ title }} </p>
        <p :class="bodyClasses + ' font-weight-bold text--darken-2'">
          {{ description }}
        </p>
        <v-row no-gutters align="center" justify="start">
          <div class="info-avatar ma-3" :style="avatarStyle">
            <img :src="avatar" style="border-radius: 50%;" width="100%">
          </div>
          <v-col :class="bodyClasses" cols="6">
            <div style="width: 20%; height: 5px" class="primary my-2"></div>
            <p
              :class="bodyClasses + ' font-weight-bold text--darken-2'"
              style="font-size: 0.85em !important;"
            >
              {{ `by ${author}` }}<br/>
              {{ postDate }}
            </p>
          </v-col>
        </v-row>
        <v-row no-gutters align="stretch" justify="end" style="height: 50px;">
          <v-btn
            min-width="35" max-width="35" min-height="50" max-height="50"
            class="mx-2 pa-0" tile elevation="0" color="primary" :href="twitter"
          >
            <v-icon>fa-twitter</v-icon>
          </v-btn>
          <v-btn
            min-width="35" max-width="35" min-height="50" max-height="50"
            class="mx-2 pa-0" tile elevation="0" color="primary" :href="facebook"
          >
            <v-icon>fa-facebook-f</v-icon>
          </v-btn>
          <v-btn
            min-width="35" max-width="35" min-height="50" max-height="50"
            class="ml-2 pa-0" tile elevation="0" color="primary" :href="email"
          >
            <v-icon>fa-envelope</v-icon>
          </v-btn>
        </v-row>
        <img :src="postImage" width="100%">
        <div class="py-6" v-html="post">

        </div>

      </v-col>
    </v-row>
  </div>
</template>

<script>
import moment from 'moment';
import TestAvatar from '../assets/images/avatars/amanda.png';
import TestImage from '../assets/images/banner_image2.png';

export default {
  data: () => ({
    title: 'This Is A Test Title',
    description: 'This Is A Test Description',
    author: 'Whoever This Is',
    post: 'Test',
    avatar: TestAvatar,
    postImage: TestImage,
    date: Date.now(),
    twitter: 'google.com',
    facebook: 'google.com',
    email: 'google.com',
  }),
  computed: {
    titleSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'title';
        case 'sm':
          return 'headline';
        case 'md':
          return 'display-1';
        case 'lg':
          return 'display-2';
        default:
          return 'display-2';
      }
    },

    bannerClasses() {
      const baseClasses = 'primary white--text display-2 text-center banner-text font-weight-black';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} ${this.titleSize} pa-0`;
        case 'sm':
          return `${baseClasses} ${this.titleSize} pa-2`;
        case 'md':
          return `${baseClasses} ${this.titleSize} pa-3 mb-8`;
        case 'lg':
          return `${baseClasses} ${this.titleSize} pa-4 mb-12`;
        default:
          return `${baseClasses} ${this.titleSize} pa-4 mb-12`;
      }
    },

    changeDescClasses() {
      const baseClasses = 'font-weight-bold grey--text text--darken-4 pa-0 ma-0';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-1`;
        case 'sm':
          return `${baseClasses} title`;
        case 'md':
          return `${baseClasses} headline`;
        case 'lg':
          return `${baseClasses} display-1`;
        default:
          return `${baseClasses} display-1`;
      }
    },

    bodyClasses() {
      const baseClasses = 'grey--text';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-2`;
        case 'sm':
          return `${baseClasses} body-1`;
        case 'md':
          return `${baseClasses} title`;
        case 'lg':
          return `${baseClasses} headline`;
        default:
          return `${baseClasses} headline`;
      }
    },

    changeSubDescPadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'px-5 py-1';
        case 'sm':
          return 'px-7 py-2';
        case 'md':
          return 'px-9 py-3';
        case 'lg':
          return 'px-11 py-4';
        default:
          return 'px-11 py-4';
      }
    },

    avatarSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 70;
        case 'sm':
          return 90;
        case 'md':
          return 110;
        case 'lg':
          return 130;
        default:
          return 150;
      }
    },

    avatarStyle() {
      return `min-width: ${this.avatarSize}px; max-width: ${this.avatarSize}px; min-height: ${this.avatarSize}px; max-height: ${this.avatarSize}px;`;
    },

    postDate() {
      return moment(new Date(this.date)).format('MMMM Do, YYYY');
    },
  },
};
</script>

<style scoped>
.underline {
  text-decoration: underline !important;
  text-decoration-color: #FFE52D !important;
}

div .upper-accent {
  border-width: 15px 0px 0px 0px;
  border-top-color: #FFE52D !important;
  border-top-style: solid;
}
</style>
